<template>
  <div class="change_password_container">
    <div class="title">비밀번호 변경 안내</div>
    <div class="content">
      <div class="content_top_info">
        회원님은 동일한 비밀번호를 90일 이상 사용하고 계십니다.
      </div>
      <div style="line-height: 1.7">
        대교는 회원님의 개인 정보를 안전하게 보호하고 개인 정보 도용으로 인한
        피해를 예방하고자 주기적으로 비밀번호 변경을 안내하고 있습니다.
      </div>
      <form style="margin-top: 20px">
        <div>
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="현재 비밀번호 입력"
            v-model="data.currentpw"
            lazy-rules
            debounce="500"
            type="password"
          />
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="새 비밀번호"
            v-model="data.newpw"
            lazy-rules
            debounce="500"
            type="password"
          />
          <InputBasic
            style="margin-top: 10px"
            outlined
            placeholder="새 비밀번호 확인"
            v-model="data.newpwconfirm"
            lazy-rules
            debounce="500"
            type="password"
          />
        </div>
        <div
          style="
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin-top: 20px;
          "
        >
          <ButtonBasic
            :clicked="onChangeLater"
            title="다음에 변경"
            style="
              width: 150px;
              margin-top: 10px;
              height: 40px;
              margin-right: 20px;
            "
          />
          <ButtonBasic
            :clicked="onSubmit"
            title="변경"
            style="width: 150px; margin-top: 10px; height: 40px"
          />
        </div>
      </form>
    </div>
  </div>
</template>

<script setup name="ChangePassword">
import InputBasic from "@/components/atom/input/InputBasic.vue";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import { reactive, watch, defineEmits, defineProps } from "vue";
import { PASSWORD } from "@/helpers/utils";
import router from "@/routers";
import AUTH_API from "@/api/modules/auth";
import { DialogStore } from "@/store/modules/dialog";

const emit = defineEmits(["onHideModal", {}]);
const dialog = DialogStore();
const { changepassword, changepasswordlater } = AUTH_API();

const props = defineProps({
  id: { type: String, require: true },
});

const { id } = props;

const data = reactive({
  currentpw: "",
  newpw: "",
  newpwconfirm: "",
});

const hideModal = () => {
  emit("onHideModal");
};

const onChangeLater = async () => {
  const res = await changepasswordlater();
  if (res) {
    hideModal();
    router.push("/");
  }
};

const onSubmit = async () => {
  if (data.currentpw === data.newpw) {
    dialog.openAlertDialog({
      message:
        "기존 비밀번호로 설정하실 수 없습니다. 비밀번호를 다시 입력해주세요.",
      buttonText: "확인",
    });
    console.log("기존비밀번호");
  } else if (data.newpw !== data.newpwconfirm) {
    dialog.openAlertDialog({
      message: "비밀번호가 동일하지 않습니다. 재입력 해 주세요.",
      buttonText: "확인",
    });
  } else if (!PASSWORD.test(data.newpw)) {
    dialog.openAlertDialog({
      message:
        "비밀밀번호는 8~20자의 영문,숫자,특수문자 조합으로 입력해주세요.",
      buttonText: "확인",
    });
  } else {
    const response = (data, statusCode) => {
      if (statusCode === "823") {
        dialog.openAlertDialog({
          message: "해당하는 사용자를 찾을수 없습니다. 다시 로그인 해주세요.",
          buttonText: "확인",
        });
        hideModal();
        return;
      } else if (statusCode === "828") {
        dialog.openAlertDialog({
          message: "현재 비밀번호를 다시 확인해주세요.",
          buttonText: "확인",
        });
        return;
      } else if (statusCode === "200") {
        dialog.openAlertDialog({
          message: "비밀번호가 변경되었습니다.",
          buttonText: "확인",
        });
        hideModal();
      }
    };
    await changepassword(
      {
        loginId: id,
        currentPassword: data.currentpw,
        password: data.newpw,
      },
      response,
    );
  }
};
</script>

<style lang="scss" scoped>
.change_password_container {
  width: 450px;
  height: 500px;
  display: flex;
  flex-direction: column;
  background-color: white;
  border: 5px solid #ddd;
  border-radius: 8px;
}
.title {
  background-color: #ddd;
  padding: 5px;
}

.content {
  padding: 50px 26px;

  &_top_info {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 30px;
  }
}
</style>
