<template>
  <div class="inquiry_container">
    <div>
      <h6>문의내용</h6>
    </div>
    <div class="divider" />
    <div class="items_row">
      <div class="item">
        <div class="label">문의아이디</div>
        <div>(아이디를 출력합니다.)</div>
      </div>
      <div class="item">
        <div class="label">이름</div>
        <div>홍길동</div>
      </div>
    </div>
    <div class="divider" />
    <div class="items_row">
      <div class="item">
        <div class="label">문의채널</div>
        <div>학습APP</div>
      </div>
      <div class="item">
        <div class="label">상태</div>
        <div>{{ data.status }}</div>
      </div>
    </div>
    <div class="divider" />
    <div class="items_row">
      <div class="item">
        <div class="label">문의 등록일시</div>
        <div>2023-07-22 12:30:20</div>
      </div>
    </div>
    <div class="divider" />
    <div class="items_row">
      <div class="item">
        <div class="label">문의제목</div>
        <div>(제목을 출력합니다.)</div>
      </div>
    </div>
    <div class="divider" />
    <div class="items_row">
      <div class="item">
        <div class="label">문의내용</div>
        <div>(내용을 출력합니다.)</div>
      </div>
    </div>
    <div class="divider" />
    <h6>문의답변</h6>
    <div class="divider" />
    <div class="items_row">
      <div class="answer" v-if="data.status === '대기'">
        <div class="label">답변<span style="color: red">*</span></div>
        <div
          style="
            display: flex;
            flex-direction: column;
            width: 90%;
            align-items: flex-end;
          "
        >
          <textarea
            v-model="answerValue"
            style="width: 100%; padding: 10px; height: 150px; resize: none"
            placeholder="답변 내용을 입력해주세요."
          />
          <div>{{ answerValue.length }}자 / 1000자</div>
        </div>
      </div>
      <div class="answer" v-if="data.status === '답변완료'">
        <div class="label">답변<span style="color: red">*</span></div>
        <div style="display: flex; flex-direction: column; width: 90%">
          <div>(등록된 내용이 출력됩니다.)</div>
          <div>답변일시 : 2023-05-02 12:10:10</div>
          <div>수정일시 : 2023-05-03 11:40:13</div>
        </div>
      </div>
    </div>
    <div class="divider" />
    <div class="items_row">
      <div class="answer" v-if="data.status === '대기'">
        <div class="label">첨부파일</div>
        <div style="display: flex; flex-direction: column">
          <div
            style="
              display: flex;
              flex-direction: row;
              width: 100%;
              align-items: center;
            "
          >
            <div class="filebox">
              <label for="file">파일찾기</label>
              <input type="file" id="file" @change="onChange" />
            </div>
            <div style="margin-left: 10px; width: 300px">
              최대 5개까지 등록 가능합니다.
            </div>
          </div>
          <div
            v-if="files.length > 0"
            style="display: flex; flex-direction: column; margin-top: 10px"
          >
            <div
              v-for="(item, index) in files"
              :key="index"
              style="display: flex; flex-directon: row"
            >
              {{ item }}
              <div class="close_button" @click="deleteItem(index)">x</div>
            </div>
          </div>
        </div>
      </div>
      <div class="answer" v-if="data.status === '답변완료'">
        <div class="label">첨부파일</div>
        <div style="display: flex; flex-direction: column">
          <div
            v-for="(item, index) in [
              'file_name1.pdf',
              'file_name2.pdf',
              'file_name3.pdf',
            ]"
            :key="index"
            style="
              display: flex;
              flex-directon: row;
              text-decoration: underline;
              color: steelblue;
            "
          >
            {{ item }}
          </div>
        </div>
      </div>
    </div>
    <div class="divider" />
    <div class="bottom_container">
      <ButtonBasic
        title="취소"
        @click="router.push('/service/inquiry')"
        color="standard"
        textColor="black"
      />
      <ButtonBasic
        :title="data.status === '답변완료' ? '수정' : '등록'"
        @click="data.status === '답변완료' ? edit() : openRegistConfirmDialog()"
        style="padding: 0 20px"
      />
    </div>
  </div>
</template>

<script setup name="InquiryDetail">
import { ref, computed } from "vue";
import { DialogStore } from "@/store/modules/dialog";
import { useRouter } from "vue-router";
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";

// TODO bottom button_container의 경우 수정 상태인 경우, 각 목록, 수정완료로 라벨이 변경되어야 함

const data = ref(history.state.data);

const store = DialogStore();
const router = useRouter();

const isEditing = ref(false);
const answerValue = ref("");
const files = ref([]);

const moveToList = (value) => {
  if (value) {
    let data = {
      answer: answerValue.value,
      files: files.value,
    };
    console.log(data);
    // TODO 현재페이지에 글이 등록되므로 목록으로 돌아가는 로직이 아닌, 새로고침하는 로직이 필요
  }
};

const edit = () => {
  isEditing.value = true;
  console.log(isEditing.value);
};

const openRegistConfirmDialog = () => {
  store.openConfirmDialog({
    message: "입력한 내용을 저장하시겠어요?",
    cancel: "취소",
    ok: "확인",
    callback: moveToList,
  });
};
const numberOfFiles = computed(() => files.value.length);

let formData = new FormData();

const onChange = (e) => {
  let selectedFiles = e.target.files;
  let max = 1048576;
  if (e.target.files.length > 0 && e.target.files[0].size > max) {
    store.openAlertDialog({
      message: "파일 용량은 1MB를 초과할 수 없습니다.",
      buttonText: "확인",
    });
    return;
  } else if (numberOfFiles.value > 4) {
    store.openAlertDialog({
      message: "파일은 5개까지 첨부할 수 있습니다.",
      buttonText: "확인",
    });
    return;
  } else {
    let fileName = e.target.value.slice(12, e.target.value.length);
    files.value.push(fileName);
    for (let i = 0; i < selectedFiles.length; i++) {
      formData.append("files", selectedFiles[i]);
    }
  }
};

const deleteItem = (index) => {
  files.value = files.value.filter((_, i) => i !== index);
};
</script>

