<template>
  <div>
    <q-markup-table class="q-my-md" flat bordered>
      <colgroup>
        <col width="20%" />
        <col width="80%" />
      </colgroup>
      <tbody>
        <td colspan="2" style="font-weight: bold; font-size: 14px">
          <div class="flex">
            <span class="table-title">{{
              data.member_type === "학원관리자"
                ? "학원관리자 수정"
                : "선생님 수정"
            }}</span>
            <span
              class="flex require"
              style="font-size: 12px; align-items: flex-end"
            >
              *필수입력
            </span>
          </div>
        </td>
        <tr>
          <td>
            회원분류
            <span class="require">*</span>
          </td>
          <td>{{ data.member_type }}</td>
        </tr>
        <tr>
          <td>학원코드(학원명)</td>
          <td>{{ data.academy_name }}</td>
        </tr>
        <tr>
          <td>사업자번호</td>
          <td>{{ data.corp_no }}</td>
        </tr>
        <tr>
          <td>아이디<span class="require">*</span></td>
          <td>{{ data.id }}</td>
        </tr>
        <tr>
          <td>비밀번호</td>
          <td>
            <button-basic
              unelevated
              color="grey"
              text-color="white"
              class="q-px-lg"
              title="비밀번호 초기화"
              @click="resetPassword"
            />
          </td>
        </tr>
        <tr>
          <td>이름<span class="require">*</span></td>
          <td></td>
        </tr>
        <tr>
          <td>휴대폰번호<span class="require">*</span></td>
          <td>
            <div class="row">
              <input-basic
                type="number"
                class="col-md-6"
                v-model="data.mobile_phone_no"
                placeholder="10-11자리 숫자만 입력(-제외)"
              />
            </div>
          </td>
        </tr>
        <tr>
          <td>생년월일</td>
          <td>
            <div class="row">
              <input-basic
                type="number"
                class="col-md-6"
                v-model="data.birth"
                placeholder="6자리 숫자만입력(-제외)"
              />
            </div>
          </td>
        </tr>
        <tr>
          <td>이메일<span class="require">*</span></td>
          <td>
            <div class="row">
              <div class="flex col-md-6">
                <input-basic
                  type="email"
                  style="flex: 1"
                  v-model="data.email"
                  placeholder="이메일 형식으로 입력"
                />
                <button-basic
                  class="q-px-lg q-ml-md"
                  outline
                  style="background: #ededed !important"
                  text-color="grey-8"
                  title="중복체크"
                  @click="confirmEmail"
                />
              </div>
            </div>
          </td>
        </tr>
        <template v-if="data.member_type === '학원관리자'">
          <tr>
            <td>소속학원</td>
            <td>{{ data.adacemy }}</td>
          </tr>
        </template>
        <tr>
          <td>최초가입일</td>
          <td>{{ data.first_access }}</td>
        </tr>
        <tr>
          <td>최종접속일</td>
          <td>{{ data.last_access }}</td>
        </tr>
        <tr>
          <td>상태</td>
          <td>
            <select-box v-model:value="data.status" style="width: 25%" />
          </td>
        </tr>
      </tbody>
    </q-markup-table>
    <div class="footer">
      <button-basic
        unelevated
        class="q-px-xl"
        color="secondary"
        title="수정"
        @click="update"
      />
      <button-basic
        class="q-px-xl q-ml-md"
        outline
        text-color="grey-8"
        title="목록"
        @click="moveToList"
      />
    </div>
  </div>
</template>
<script setup>
import ButtonBasic from "@/components/atom/button/ButtonBasic.vue";
import InputBasic from "@/components/atom/input/InputBasic.vue";
import SelectBox from "@/components/molecule/select/SelectBox.vue";
import { ref, reactive, onMounted } from "vue";
import { useRouter, useRoute } from "vue-router";

const router = useRouter();
const route = useRoute();
const id = ref("");

const data = reactive({
  member_type: "학원관리자",
  academy_name: "",
  corp_no: "",
  id: "",
  mobile_phone_no: "",
  birth: "",
  email: "",
  status: "",
  adacemy: "",
  first_access: "",
  last_access: "",
});

const resetPassword = () => {
  console.log("reset password");
};

const moveToList = () => {
  router.push("/member/academy");
};

const update = () => {
  router.push(`/member/academy`);
};

onMounted(() => {
  id.value = route.params.id;
});
</script>
<style scoped>
td {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.footer {
  display: flex;
  justify-content: center;
  padding-bottom: 1rem;
}
</style>
